/**
 * MainNavigation Component
 * Handles the main site navigation with logo, search, and category tags
 */

class MainNavigation {
  constructor(container, options = {}) {
    this.container = container;
    this.options = {
      showSearch: true,
      showSubscribe: true,
      showBookmarks: true,
      logoUrl: 'assets/logo-v3.svg',
      homeUrl: '/',
      ...options,
    };

    this.categories = [
      'Technology',
      'Science',
      'World',
      'Business',
      'Health',
      'Environment',
      'Sports',
      'Entertainment',
      'Finance',
      'Education',
      'Culture',
      'Politics',
      'Travel',
      'Food',
      'Lifestyle',
      'Opinion',
      'Local',
      'Investigative',
      'Climate',
      'International',
    ];

    this.init();
  }

  init() {
    this.render();
    this.bindEvents();
  }

  render() {
    this.container.innerHTML = this.getTemplate();
  }

  getTemplate() {
    return `
      <header class="sticky top-0 z-50 bg-white shadow-md">
        <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div class="flex flex-col py-3">
            <!-- Top row with title, tagline, search and subscribe -->
            <div class="flex items-center justify-between mb-4">
              ${this.getLogoSection()}
              ${this.getCategoriesSection()}
              ${this.getActionsSection()}
            </div>
          </div>
        </div>
      </header>
    `;
  }

  getLogoSection() {
    return `
      <div class="flex items-center gap-3">
        <img
          src="${this.options.logoUrl}"
          alt="The News Curator Logo"
          style="height: 150px; width: 250px; cursor: pointer"
          onclick="window.location.href='${this.options.homeUrl}'"
        />
        <div class="flex flex-col">
          <h1 class="font-serif text-3xl font-bold tracking-tight whitespace-nowrap text-gray-900"></h1>
          <p class="text-sm text-black italic font-medium"></p>
        </div>
      </div>
    `;
  }

  getCategoriesSection() {
    const categoryRows = this.getCategoryRows();

    return `
      <div class="hidden lg:flex flex-col items-center gap-2">
        ${categoryRows
          .map(
            (row) => `
          <div class="flex items-center gap-2 flex-wrap justify-center">
            ${row.map((category) => this.getCategoryLink(category)).join('')}
          </div>
        `
          )
          .join('')}
      </div>
    `;
  }

  getCategoryRows() {
    const rows = [];
    const categoriesPerRow = 7;

    for (let i = 0; i < this.categories.length; i += categoriesPerRow) {
      rows.push(this.categories.slice(i, i + categoriesPerRow));
    }

    return rows;
  }

  getCategoryLink(category) {
    const isAbout = category === 'About';
    const href = isAbout
      ? 'transparency-page.html'
      : `index.html?tag=${category.toLowerCase()}`;

    return `
      <a
        class="bg-black bg-opacity-70 hover:bg-opacity-90 text-white px-2 py-1 rounded-full text-xs font-medium transition-colors"
        href="${href}"
      >
        ${category}
      </a>
    `;
  }

  getActionsSection() {
    return `
      <div class="flex flex-col items-start gap-2">
        ${this.options.showSearch ? this.getSearchSection() : ''}
        <div class="flex items-center gap-3">
          ${this.options.showSubscribe ? this.getSubscribeButton() : ''}
          ${this.options.showBookmarks ? this.getBookmarksButton() : ''}
          ${this.getMobileMenuButton()}
        </div>
      </div>
    `;
  }

  getSearchSection() {
    return `
      <label class="relative hidden sm:block">
        <span class="material-symbols-outlined absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">search</span>
        <input
          id="search-input"
          class="form-input w-80 min-w-0 resize-none overflow-hidden rounded-full bg-white border-2 border-gray-300 focus:border-primary focus:ring-2 focus:ring-primary/20 pl-10 pr-4 py-4 text-base shadow-md"
          placeholder="Search news..."
          type="search"
        />
      </label>
    `;
  }

  getSubscribeButton() {
    return `
      <button
        onclick="document.getElementById('newsletter-form').scrollIntoView({behavior: 'smooth'})"
        class="bg-primary text-white px-4 py-2 rounded-lg font-medium hover:bg-primary/90 transition-colors whitespace-nowrap"
      >
        Subscribe
      </button>
    `;
  }

  getBookmarksButton() {
    return `
      <a
        href="/bookmarks"
        class="bg-gray-200 text-gray-700 px-4 py-2 rounded-lg font-medium hover:bg-gray-300 transition-colors whitespace-nowrap"
      >
        Bookmarks
      </a>
    `;
  }

  getMobileMenuButton() {
    return `
      <button
        class="mobile-menu-button md:hidden flex flex-col justify-center items-center w-8 h-8 bg-transparent border-none cursor-pointer"
      >
        <span class="block w-6 h-0.5 bg-gray-900 dark:bg-white transition-all duration-300"></span>
        <span class="block w-6 h-0.5 bg-gray-900 dark:bg-white mt-1 transition-all duration-300"></span>
        <span class="block w-6 h-0.5 bg-gray-900 dark:bg-white mt-1 transition-all duration-300"></span>
      </button>
    `;
  }

  bindEvents() {
    this.setupSearch();
    this.setupMobileMenu();
  }

  setupSearch() {
    const searchInput = this.container.querySelector('#search-input');
    if (searchInput) {
      searchInput.addEventListener('keypress', (e) => {
        if (e.key === 'Enter') {
          this.handleSearch(e.target.value);
        }
      });
    }
  }

  setupMobileMenu() {
    const mobileMenuButton = this.container.querySelector(
      '.mobile-menu-button'
    );
    if (mobileMenuButton) {
      mobileMenuButton.addEventListener('click', () => {
        this.toggleMobileMenu();
      });
    }
  }

  handleSearch(query) {
    if (query.trim()) {
      window.location.href = `index.html?search=${encodeURIComponent(query)}`;
    }
  }

  toggleMobileMenu() {
    // This will be handled by the MobileNavigation component
    const event = new CustomEvent('toggleMobileMenu');
    document.dispatchEvent(event);
  }

  // Public API methods
  updateCategories(categories) {
    this.categories = categories;
    this.render();
    this.bindEvents();
  }

  setActiveCategory(category) {
    const links = this.container.querySelectorAll('a[href*="tag="]');
    links.forEach((link) => {
      link.classList.remove('bg-primary', 'bg-opacity-100');
      link.classList.add('bg-black', 'bg-opacity-70');

      if (link.href.includes(`tag=${category.toLowerCase()}`)) {
        link.classList.remove('bg-black', 'bg-opacity-70');
        link.classList.add('bg-primary', 'bg-opacity-100');
      }
    });
  }
}

// Export for use in other modules
if (typeof module !== 'undefined' && module.exports) {
  module.exports = MainNavigation;
} else {
  window.MainNavigation = MainNavigation;
}
