/**
 * ArticleCard Component
 * Displays article information in a card format for lists and grids
 */

class ArticleCard {
  constructor(article, options = {}) {
    this.article = article;
    this.options = {
      showImage: true,
      showSummary: true,
      showCategory: true,
      showDate: false,
      showAuthor: false,
      summaryLength: 150,
      imageHeight: 'h-48',
      onClick: null,
      className: '',
      ...options,
    };

    this.element = null;
    this.create();
  }

  create() {
    this.element = document.createElement('article');
    this.element.className = this.getCardClasses();
    this.element.innerHTML = this.getTemplate();
    this.bindEvents();
  }

  getCardClasses() {
    const baseClasses =
      'bg-white dark:bg-gray-800 rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-shadow cursor-pointer';
    return `${baseClasses} ${this.options.className}`;
  }

  getTemplate() {
    return `
      ${this.options.showImage ? this.getImageTemplate() : ''}
      <div class="p-4">
        ${this.getTitleTemplate()}
        ${this.options.showSummary ? this.getSummaryTemplate() : ''}
        <div class="flex items-center justify-between mt-3">
          ${this.options.showCategory ? this.getCategoryTemplate() : ''}
          ${this.getMetadataTemplate()}
        </div>
      </div>
    `;
  }

  getImageTemplate() {
    if (this.article.image_url) {
      return `
        <div class="${this.options.imageHeight} bg-cover bg-center" 
             style="background-image: url('${this.article.image_url}')">
        </div>
      `;
    } else {
      // Fallback gradient background
      const gradients = [
        'from-blue-500 to-blue-600',
        'from-green-500 to-green-600',
        'from-purple-500 to-purple-600',
        'from-red-500 to-red-600',
        'from-yellow-500 to-yellow-600',
        'from-indigo-500 to-indigo-600',
      ];

      const gradient =
        gradients[
          Math.abs(this.hashCode(this.article.title || '')) % gradients.length
        ];

      return `
        <div class="${this.options.imageHeight} bg-gradient-to-br ${gradient}">
        </div>
      `;
    }
  }

  getTitleTemplate() {
    const title = this.article.title || 'Untitled Article';
    return `
      <h3 class="font-bold text-lg mb-2 text-gray-900 dark:text-white line-clamp-2">
        ${this.escapeHtml(title)}
      </h3>
    `;
  }

  getSummaryTemplate() {
    const summary = this.article.summary || this.article.content || '';
    const truncatedSummary = this.truncateText(
      summary,
      this.options.summaryLength
    );
    const fullSummary = this.escapeHtml(summary);

    return `
      <p class="text-gray-600 dark:text-gray-300 text-sm mb-3 line-clamp-3" 
         data-full-summary="${fullSummary}"
         title="${fullSummary}">
        ${this.escapeHtml(truncatedSummary)}
      </p>
    `;
  }

  getCategoryTemplate() {
    const category = this.getCategory();
    if (!category) return '';

    return `
      <span class="text-xs text-primary font-medium bg-primary/10 px-2 py-1 rounded-full">
        ${this.escapeHtml(category)}
      </span>
    `;
  }

  getMetadataTemplate() {
    const metadata = [];

    if (this.options.showDate && this.article.created_at) {
      const date = new Date(this.article.created_at);
      const formattedDate = date.toLocaleDateString('en-US', {
        month: 'short',
        day: 'numeric',
      });
      metadata.push(
        `<span class="text-xs text-gray-500">${formattedDate}</span>`
      );
    }

    if (this.options.showAuthor && this.article.author) {
      metadata.push(
        `<span class="text-xs text-gray-500">by ${this.escapeHtml(
          this.article.author
        )}</span>`
      );
    }

    // Reading time estimate
    if (this.article.content) {
      const wordCount = this.article.content.split(' ').length;
      const readingTime = Math.ceil(wordCount / 200);
      metadata.push(
        `<span class="text-xs text-gray-500">${readingTime} min read</span>`
      );
    }

    return metadata.length > 0
      ? `<div class="flex items-center space-x-2">${metadata.join('')}</div>`
      : '';
  }

  bindEvents() {
    if (this.element) {
      this.element.addEventListener('click', () => {
        if (this.options.onClick) {
          this.options.onClick(this.article);
        } else {
          this.navigateToArticle();
        }
      });

      // Summary hover functionality
      const summaryElement = this.element.querySelector('[data-full-summary]');
      if (summaryElement) {
        this.setupSummaryHover(summaryElement);
      }
    }
  }

  /**
   * Attach event listeners to an element (for testing)
   * @param {HTMLElement} element - Element to attach events to
   */
  attachEvents(element) {
    if (element && element.addEventListener) {
      element.addEventListener('click', () => {
        if (this.options.onClick) {
          this.options.onClick(this.article);
        } else {
          this.navigateToArticle();
        }
      });
    }
  }

  setupSummaryHover(summaryElement) {
    let hoverTimeout;
    let popup;

    summaryElement.addEventListener('mouseenter', () => {
      hoverTimeout = setTimeout(() => {
        const fullSummary = summaryElement.getAttribute('data-full-summary');
        if (fullSummary && fullSummary.length > this.options.summaryLength) {
          popup = this.createSummaryPopup(fullSummary, summaryElement);
        }
      }, 500);
    });

    summaryElement.addEventListener('mouseleave', () => {
      clearTimeout(hoverTimeout);
      if (popup) {
        popup.remove();
        popup = null;
      }
    });
  }

  createSummaryPopup(fullSummary, targetElement) {
    const popup = document.createElement('div');
    popup.className =
      'absolute z-50 bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg shadow-lg p-4 max-w-sm';
    popup.style.cssText = 'max-height: 200px; overflow-y: auto;';

    popup.innerHTML = `
      <p class="text-sm text-gray-700 dark:text-gray-300 leading-relaxed">
        ${this.escapeHtml(fullSummary)}
      </p>
    `;

    // Position popup
    const rect = targetElement.getBoundingClientRect();
    popup.style.position = 'fixed';
    popup.style.top = `${rect.bottom + 10}px`;
    popup.style.left = `${rect.left}px`;

    document.body.appendChild(popup);
    return popup;
  }

  navigateToArticle() {
    if (this.article.slug) {
      window.location.href = `article.html?slug=${this.article.slug}`;
    } else if (this.article._id) {
      window.location.href = `article.html?id=${this.article._id}`;
    }
  }

  // Utility methods
  getCategory() {
    if (this.article.category) return this.article.category;
    if (this.article.tags) {
      const tags = this.article.tags.split(',').map((tag) => tag.trim());
      return tags.length > 0 ? tags[0] : null;
    }
    return null;
  }

  truncateText(text, maxLength) {
    if (!text || text.length <= maxLength) return text;
    return text.substring(0, maxLength).trim() + '...';
  }

  escapeHtml(text) {
    const div = document.createElement('div');
    div.textContent = text;
    return div.innerHTML;
  }

  hashCode(str) {
    let hash = 0;
    for (let i = 0; i < str.length; i++) {
      const char = str.charCodeAt(i);
      hash = (hash << 5) - hash + char;
      hash = hash & hash; // Convert to 32bit integer
    }
    return hash;
  }

  // Public API methods
  getElement() {
    return this.element;
  }

  /**
   * Render method for testing compatibility
   * @returns {string} HTML string
   */
  render() {
    return this.template(this.article);
  }

  /**
   * Simple template method for testing compatibility
   * @param {Object} data - Article data
   * @returns {string} HTML template
   */
  template(data) {
    if (!data) {
      return '<div class="article-card article-card--empty">No article data</div>';
    }

    // Escape HTML to prevent XSS
    const escapeHtml = (text) => {
      if (!text) return '';
      return text
        .replace(/&/g, '&amp;')
        .replace(/</g, '&lt;')
        .replace(/>/g, '&gt;')
        .replace(/"/g, '&quot;')
        .replace(/'/g, '&#39;');
    };

    const compactClass = this.options.compact ? ' article-card--compact' : '';
    const imageHtml = data.image_url
      ? `<div class="article-card__image">
           <img src="${escapeHtml(data.image_url)}" alt="${escapeHtml(
          data.title
        )}" loading="lazy">
         </div>`
      : `<div class="article-card__image article-card__image--placeholder">
           <div class="placeholder-icon">üì∞</div>
         </div>`;

    return `
      <article class="article-card${compactClass}" data-slug="${escapeHtml(
      data.slug || ''
    )}" role="article" aria-label="Article: ${escapeHtml(
      data.title || 'Untitled'
    )}">
        ${imageHtml}
        <div class="article-card__content">
          <h2 class="article-card__title">${escapeHtml(
            data.title || 'Untitled'
          )}</h2>
          <p class="article-card__category">${escapeHtml(
            data.category || 'Uncategorized'
          )}</p>
          ${
            data.summary
              ? `<p class="article-card__summary">${escapeHtml(
                  data.summary
                )}</p>`
              : ''
          }
        </div>
      </article>
    `;
  }

  updateArticle(article) {
    this.article = article;
    this.element.innerHTML = this.getTemplate();
    this.bindEvents();
  }

  setLoading(loading) {
    if (loading) {
      this.element.classList.add('opacity-50', 'pointer-events-none');
    } else {
      this.element.classList.remove('opacity-50', 'pointer-events-none');
    }
  }

  appendTo(container) {
    if (container && this.element) {
      container.appendChild(this.element);
    }
    return this;
  }

  remove() {
    if (this.element && this.element.parentNode) {
      this.element.parentNode.removeChild(this.element);
    }
  }

  // Static factory methods
  static create(article, options = {}) {
    return new ArticleCard(article, options);
  }

  static createList(articles, container, options = {}) {
    const cards = articles.map((article) => new ArticleCard(article, options));

    if (container) {
      cards.forEach((card) => card.appendTo(container));
    }

    return cards;
  }
}

// Export for use in other modules
if (typeof module !== 'undefined' && module.exports) {
  module.exports = ArticleCard;
} else {
  window.ArticleCard = ArticleCard;
}
