// CSS is handled by the build process
const MainNavigation = require('../components/navigation/MainNavigation.js');
const ArticleCard = require('../components/article/ArticleCard.js');
const ErrorHandler = require('../services/error/ErrorHandler.js');
class App {
  constructor() {
    this.init();
  }
  init() {
    try {
      const navContainer = document.querySelector('nav');
      if (navContainer) {
        new MainNavigation(navContainer);
      }
      this.initializeArticleCards();
      this.setupErrorHandling();
      console.log('Application initialized successfully');
    } catch (error) {
      ErrorHandler.handle(error, { context: 'App initialization' });
    }
  }
  initializeArticleCards() {
    const articleElements = document.querySelectorAll('[data-article]');
    articleElements.forEach((element) => {
      const articleData = JSON.parse(element.dataset.article);
      new ArticleCard(element, articleData);
    });
  }
  setupErrorHandling() {
    window.addEventListener('error', (event) => {
      ErrorHandler.handle(event.error, {
        context: 'Global error handler',
        filename: event.filename,
        lineno: event.lineno,
      });
    });
    window.addEventListener('unhandledrejection', (event) => {
      ErrorHandler.handle(event.reason, {
        context: 'Unhandled promise rejection',
      });
    });
  }
}
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', () => new App());
} else {
  new App();
}
// Export for both CommonJS and ES modules
if (typeof module !== 'undefined' && module.exports) {
  module.exports = App;
} else if (typeof window !== 'undefined') {
  window.App = App;
}
